import React from "react";
import logo from "../../styles/assets/images/Logo/favicon-removebg.png";
import { Fragment } from "react";

const Header = () => {
  return (
    <Fragment>
      <header className=" headers">
        <a id="home" href="#">
          <img className="logoheader" alt="Justtry logo" src={logo} />
        </a>
        <nav className="main-nav">
          <ul className="main-nav-list">
            <li>
              <a className=" main-nav-link" href="#services">
                services
              </a>
            </li>
            <li>
              <a className=" main-nav-link" href="#blogs">
                About us
              </a>
            </li>
            <li>
              <a className=" main-nav-link" href="#testimonial">
                Testimonials
              </a>
            </li>
            <li>
              <a className=" main-nav-link" href="#ourteam">
                our team
              </a>
            </li>
            {/* <li>
            <a className='rightAnimation main-nav-link' id="blog-hover" href='#ourteam'>
              blogs
            </a>
          </li> */}
            <li>
              <a className=" main-nav-link nav-cta" href="#talktous">
                TALK TO US
              </a>
            </li>
          </ul>
          {/* <ul className="blog_hover-section">
          <li>Blog 1</li>
          <li>Blog 2</li>
          <li>Blog 3</li>
          <li>Blog 4</li>
        </ul> */}
        </nav>
        {/* <button className='btn-mobile-nav'>
        <ion-icon className='icon-mobile-nav menu' name='menu-outline'></ion-icon>
        <ion-icon className='icon-mobile-nav close' name='close-outline'></ion-icon>
      </button> */}
      </header>
    </Fragment>
  );
};

export default Header;
